$gold: #F6DB86;
$purple: #1E306E;

body {
	font-family: "Geometria"; }
@media (min-width: 1300px) {
	.container {
		width: 1270px; } }
@media (min-width: 992px) {
	body {
		.header {
			.navigation__wrapper {}
			.header__background {}
			&--big {
				.header__background {}
				& .navigation__wrapper {} }
			&--small {
				.subNavigation {
					background-color: rgba(0, 30, 66, 0.85);
					border-radius: 8px;
					margin-top: 10px;
					padding-top: 10px;
					padding-bottom: 10px; } }
			&__infoWrapper {}
			&__info {}
			&__pageTitle {}
			&__pageTagline {}
			&__pageTaglineRow {}
			&--noBackground {
				& .subNavigation {
					background-color: rgba(255, 255, 255, .95);
					border-radius: 8px; } } } } }

.main {
	min-height: 400px; }
.fullHeight {
	height: 100%; }
