.header {
	height: 440px;
	overflow-x: hidden;
	position: relative;
	.navigation__wrapper {
		margin-top: 20px;
		position: relative;
		z-index: 1; }
	.header__background {
		height: 100%;
		width: 100%;
		background-position: center;
		background-repeat: no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1; }
	&--big {
		height: 760px;
		.header__background {
			height: 720px; }
		& .navigation__wrapper {
			margin-top: 30px; } }
	&--small {
		// background: linear-gradient(0deg, rgba(0, 30, 66, 0.5), rgba(0, 30, 66, 0.5))
		// display: flex
		// justify-content: center
 }		// align-items: center
	&__infoWrapper {
		height: 100%;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		left: 0;
		right: 0; }
	&__info {
		max-width: 744px;
		width: 100%; }
	&__pageTitle {}
	&__pageTagline {
		color: #fff;
		font-size: 18px;
		line-height: 1.5;
		margin-top: 10px; }
	&__pageTaglineRow {
		display: block; }
	&--noBackground {
		height: 320px;
		& .subNavigation {
			padding-top: 10px;
			padding-bottom: 10px; }
		& .subNavigation__item {
			margin-top: 4px;
			margin-bottom: 4px; }
		& .navigation a,
		& .navigation .subNavigation__link {
			color: $darkBlue; }
		& .navigation .language {
			color: $darkBlue;
			opacity: 1;
			&:hover,
			&--active {
				color: $lightGreen;
				opacity: 1; } }
		& .logo__wrapper .logo {
			background-image: url("../assets/img/logo/logo_dark.png"); }
		& .logo__wrapper .logo__title {
			color: $darkBlue; }
		& .section__title {
			position: relative;
			&::before {
				content: "";
				height: 0.0625em;
				width: 1.66667em;
				background-color: $gold;
				display: block;
				margin: 0 auto;
				position: absolute;
				left: 0;
				right: 0;
				bottom: -0.625em; } } } }



.navigation__wrapper {
	font-size: 16px; }
.navigation {
	// width: 12.5em
	width: 520px;
	border-radius: 80px;
	padding: 0.66667em 1em;
	&--withoutLogo {
		justify-content: flex-end; }
	&.navigation--dark {
		background-color: $darkBlue;
		& .subNavigation {
			background-color: $darkBlue; }
		.joinUs {
			border: 2px solid #899AC7; } }
	& ul {
		// font-size: 16px
