		margin-right: .6em; }
	.languages {
		margin-left: 1em;
		.language {
			margin-left: .2em;
			margin-right: .2em;
			opacity: .7;
			transition: .3s;
			&:hover {
				opacity: 1; }
			&--active {
				opacity: 1; } } }
	.joinUs {
		font-size: 1em;
		text-align: center;
		border: 2px solid #A5CDE9;
		border-radius: 80px;
		margin-left: 1.3333em;
		padding: 1.07em 1.71em;
		transition: .1s ease-in-out;
		&:hover {
			background-color: #899AC7;
			transition: .1s ease-in-out; } } }
.subNavigation__switcher {
	position: relative;
	&:hover .subNavigation {
		opacity: 1;
		transform: scaleY(1);
		transition: .2s ease-in-out; } }
.subNavigation {
	width: 250px;
	border-radius: 0px 0px 8px 8px;
	padding-top: 40px;
	padding-bottom: 30px;
	padding-left: 30px;
	// padding-right: 30px
	position: absolute;
	top: 17px;
	left: -26px;
	opacity: 0;
	transform: scaleY(0);
	transform-origin: 0 0;
	transition: .2s ease-in-out;
	&__item {
		margin-top: 8px;
		margin-bottom: 8px;
		position: relative;
		transition: .2s ease-in-out;
		&:hover {
			padding-left: 10px;
			transition: .2s ease-in-out; }
		&::before {
			content: url("../assets/img/icons/subMenu_arrow.png");
			height: 16px;
			width: 14px;
			background-position: center;
			background-repeat: no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
			position: absolute;
			top: 8px;
			left: -20px; }
		&:hover .subNavigation__link {
			color: #A5CDE9;
			transition: .2s ease-in-out; } }
	&__link {
		color: #fff;
		font-family: "Geometria";
		font-size: 16px;
		line-height: 30px; } }
.logo__wrapper {
