.faqsContainer {
	padding-top: 80px;
	padding-bottom: 94px; }
.faq {
	max-width: 800px;
	width: 100%;
	margin: 0 auto;
	&__subtitle {}
	&__title {}
	.faq__question {
		color: #10192C;
		font-family: "Geometria-Bold";
		font-size: 20px;
		font-weight: 600;
		background-color: transparent;
		border: 1px solid transparent;
		padding-left: 0;
		padding-right: 40px;
		padding-top: 30px;
		padding-bottom: 30px;
		position: relative;
		&:not(:first-child) {
			border-top: 1px solid #E6E6E6; }
		&::before {
			// content: "\f067"
			content: "";
			height: 30px;
			width: 30px;
			color: #10192C;
			line-height: 30px;
			font-size: 24px;
			font-family: "FontAwesome";
			text-align: center;
			background-image: url("../assets/img/icons/accordion_close.png");
			background-repeat: no-repeat;
			background-position: center;
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			z-index: 1;
			transform: translateY(-50%);
			transition: 0.1s; }
		&[aria-expanded="true"]::before {
			transform: translateY(-50%) rotate(45deg);
			transition: 0.1s; }
		&::after {
			content: "";
			height: 30px;
			width: 30px;
			border-radius: 50%;
			display: block;
			position: absolute;
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			transition: 0.1s; }
		&[aria-expanded="false"]::after {
			background-color: transparent;
			border: 1px solid #F5F4F9; }
		&[aria-expanded="true"]::after {
			background-color: #F5F4F9;
			border: 1px solid transparent; } }
	.faq__answer {
		color: $textColor;
		font-size: 18px;
		line-height: 1.5;
		background-color: transparent;
		border: 1px solid transparent;
		padding-left: 0; } }

